import { Component } from '@angular/core';
import { ModalController, NavController } from 'ionic-angular';
import { NavParams } from 'ionic-angular';
import { DataProvider } from '../../providers/data-provider';
import { BusinessesService } from '../../services/businesses.service';
import { BusinessDetailPage } from './business-detail';
import { BusinessesFilter } from './businesses-filter';

@Component({
	selector: 'page-businesses',
	templateUrl: './businesses.html'
})
export class BusinessesPage {
	queryText = '';
	businesses: any[] = [];
	orderBy: 'name' | 'distance' = 'name';

	get isFilterDirty(): boolean {
		return !!this.selectedCategory;
	}

	categories: any[] = [];
	selectedCategoryData: any;
	selectedCategory: string = null;

	constructor(
		private modalCtrl: ModalController,
		private navCtrl: NavController,
		navParams: NavParams,
		public service: BusinessesService,
		private data: DataProvider
	) {
		if (navParams.data.category) {
			this.selectedCategory = navParams.get('category').key;
		}
	}

	ionViewCanEnter(): Promise<boolean> {
		return this.data.init();
	}

	ionViewDidLoad() {
		this.updateList();
	}

	ionViewWillEnter() {
		Promise.all([this.loadCategories()])
			.then(() => {
				this.updateList();
				this.selectedCategoryData = this.loadSelectedCategory(this.selectedCategory);
			});
	}

	updateList() {
		this.service.fetchBusinesses(this.queryText, false, this.selectedCategory, this.orderBy)
			.then(businesses => {
				this.businesses = businesses;
			});
	}

	presentFilter() {
		let modal = this.modalCtrl.create(BusinessesFilter, {
			categories: this.categories,
			orderBy: this.orderBy,
			selectedCategory: this.selectedCategory
		});
		modal.present();

		modal.onWillDismiss((result) => {
			if (result) {
				this.selectedCategory = result.selectedCategory;
				this.orderBy = result.orderBy;
				this.updateList();
				this.selectedCategoryData = this.loadSelectedCategory(this.selectedCategory);
			}
		});
	}

	goToBusinessDetail(business: any) {
		this.service.setCurrent(business);
		this.navCtrl.push(BusinessDetailPage);
	}

	clearFilter() {
		this.selectedCategory = null;
		this.updateList();
		this.selectedCategoryData = null;
	}

	selectCategory(category) {
		this.selectedCategory = category.$key;
		this.updateList();
		this.selectedCategoryData = this.loadSelectedCategory(this.selectedCategory);
	}

	private loadCategories() {
		return this.service.getCategories().then(categories => {
			this.categories = [{ $key: null, name: 'All' }, ...categories];
		});
	}

	private loadSelectedCategory(selectedCategory) {
		return this.categories.find(function(category) {
			if (category.$key) {
				return category.$key == selectedCategory;
			}
		});
	}
}



