import { Component, OnInit } from '@angular/core';
import { NavController } from 'ionic-angular';
import { HomeService } from '../../services/home.service';
import { BusinessesPage } from '../businesses/businesses';
import { BusinessDetailPage } from '../businesses/business-detail';
import { SlideshowPage } from '../slideshow/slideshow.page';
import { BusinessesService } from '../../services/businesses.service';
import { InAppBrowserService } from '../../common/services/in-app-browser.service';

@Component({
	selector: 'page-home',
	templateUrl: './home.html'
})

export class HomePage implements OnInit {
  homeData: any = {};
	business: any = {};
	businesses: any[] = [];
  categories: any[] = [];
	itemRows: any[][][][] = [];

  constructor(
		private navCtrl: NavController,
		private inBrowser: InAppBrowserService,
		public service: HomeService,
		public businessesService: BusinessesService
  ) {
  }

	ngOnInit(): void {
		this.service.getHomeData().then(homeData => {
      this.homeData = homeData;
      this.categories = Object.keys(this.homeData.categories).map(key => this.homeData.categories[key]);
			this.service.getBusinesses().then(businesses => {
				this.businesses = businesses;
				this.itemRows = this.createItemRows();
	  	});
  	});
	}

	itemTapped(item: any) {
		if (item.isBusiness) {
			this.goToBusiness(item.key);
		} else if (item.isAction) {
			this.openUrl(item.url);
		} else if (item.isSlideshow) {
			this.goToSlideshow();
		} else {
			this.goToBusinesses(item);
		}
	}

  goToBusinesses(category: any) {
  	this.navCtrl.push(BusinessesPage, { category: category });
  }

	goToBusiness(business: string) {
		this.service.getBusiness(business)
			.then(business => {
				this.business = business;
				this.businessesService.setCurrent(this.business);
				this.navCtrl.push(BusinessDetailPage);
			});
	}

	openUrl(url: string) {
		this.inBrowser.open(url);
	}

	goToSlideshow() {
  	this.navCtrl.push(SlideshowPage);
  }

	private createItemRows() {
		let row = [];
		this.categories.forEach(item => {
			row.push(item);
			if (row.length === 4) {
				this.itemRows.push(row);
				row = [];
			}
		});
		this.businesses.forEach(business => {
			row.push(business);
			if (row.length === 4) {
				this.itemRows.push(row);
				row = [];
			}
		});
		if (row.length) {
			this.itemRows.push(row);
		}
		return this.itemRows;
	}
}



