import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { DataProvider } from '../providers/data-provider';

@Injectable()
export class HomeService {

  constructor(private http: Http, private dataProvider: DataProvider) {
  }

  getHomeData(): Promise<any[]> {
    let homeDataUrl = 'assets/data/home.json';
    return this.http.get(homeDataUrl)
      .map(x => x.json())
      .toPromise()
      .then(response => {
        Object.keys(response.categories).forEach(function(key) {
          response.categories[key].key = key;
        });
        return response;
      });
    }

    getBusinesses(): Promise<any[]> {
      let businessesDataUrl = 'assets/data/businesses.json';
      return this.http.get(businessesDataUrl)
        .map(x => x.json())
        .toPromise()
        .then(response => {
          return response.businesses;
        });
    }

    getBusiness(businessId: string) {
      return this.dataProvider.getBusiness(businessId);
    }
}



